.services__container {
    grid-template-columns: repeat(3, 270px);
    justify-content: center;
    column-gap: 1.8rem;
}

.services__content {
    position: relative;
    border: 1px solid rgba(0, 0, 0, 0.1);
    background-color: var(--container-color);
    padding: 6rem 0 2rem 2.5rem;
}

.services__icon {
    display: block;
    font-size: 2rem;
    color: var(--title-color);
    margin-bottom: var(--mb-1);
}

.services__title {
    font-size: var(--h3-font-size);
    margin-bottom: var(--mb-1);
    font-weight: var(--font-medium);
}

.services__button {
    color: var(--title-color);
    font-size: var(--small-font-size);
    display: inline-flex;
    align-items: center;
    column-gap: 0.25rem;
    cursor: pointer;
}

.services__button-icon {
    font-size: 1rem;
    transition: 0.3s;
}

.services__button:hover .services__button-icon{
    transform: translateX(00.25rem);
}

.services__modal {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: var(--z-modal);
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 1rem;
    opacity: 0;
    visibility: hidden;
    transition: .3s;
}

.services__modal-content {
    width: 650px;
    position: relative;
    background-color: var(--container-color);
    padding: 3rem 2.5rem 2.5rem;
    border-radius: 1.5rem;
    height: auto;
    text-align: center;
}

.services__modal-close {
    position: absolute;
    top: 1.5rem;
    right: 1.5rem;
    font-size: 1.5rem;
    color: var(--title-color);
    cursor: pointer;
}



.services__modal-title {
    font-size: var(--h3-font-size);
    font-weight: var(--font-medium);
    margin-bottom: var(--mb-1);
    text-align: center;
}

.services__modal-description {
    font-size: var(--small-font-size);
    padding: 0 1.5rem;
    margin-bottom: var(--mb-2);
    max-height: 200px; /* Set a maximum height for the content */
    overflow-y: auto; /* Enable vertical scrolling */
    overflow-x: hidden; /* Disable horizontal scrolling */
    white-space: normal; /* Allow text to wrap normally */
    text-align: left;
}
.services__modal-techStack {
    font-size: var(--small-font-size);
    padding: 0 0.5rem;
}

.services__modal-project-link, .services__modal-techStack-container{
    display: flex;
    padding: 0 1.5rem;
}

.services__modal-githublink-icon {
    color: navy;
}

.services__modal-githublink {
    padding: 0 0.5rem;
    font-size: var(--small-font-size);
    cursor: pointer;
    color: navy;
}


.services__modal-services {
    row-gap: 0.75rem;
}

.services__modal-service {
    display: flex;
    align-items: center;
    column-gap: 1rem;
}

.services__modal-icon {
    color: var(--title-color);
    font-size: 1.1rem;
}

.services__modal-info {
    font-size: var(--small-font-size);
    text-align: left;
}

/* Active Modal */
.active-modal {
    opacity: 1;
    visibility: visible;
}

/*-----------Breakpoints-------------*/ 
/* For medium and small devices*/
@media screen and (max-width: 576px) {
    .services__modal-content {
        width: 90%;
        padding: 2rem 1.5rem 1.5rem;
        
    }
    .services__modal-description {
        max-height: 150px;
    }

    .services__modal-description, .services__modal-project-link, .services__modal-techStack-container {
        padding: 0;
    }
    .services__modal-techStack {
        text-align: left;
    }
    .services__modal-close{
        top: 0.2rem;;
    }
}
